<template>
  <div>
    <button
      type="button"
      class="btn btn-primary"
      data-bs-toggle="modal"
      data-bs-target="#B_MenuPlus"
    >
      메뉴 추가하기
    </button>
    <div
      class="modal fade"
      id="B_MenuPlus"
      tabindex="-1"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h1 class="modal-title fs-5" id="exampleModalLabel">
              메뉴 추가하기
            </h1>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body">
            <form @submit.prevent="saveChanges">
              <div class="mb-2">
                <label for="menuName" class="form-label">메뉴 이름</label>
                <input
                  type="text"
                  class="form-control"
                  id="menuName"
                  v-model="fname"
                  required
                />
              </div>

              <div class="mb-3">
                <label for="menuDescription" class="form-label"
                  >메뉴 설명</label
                >
                <textarea
                  class="form-control"
                  id="menuDescription"
                  v-model="finfo"
                  required
                ></textarea>
              </div>

              <div class="mb-3">
                <label for="menuPrice" class="form-label">메뉴 가격</label>
                <input
                  type="text"
                  class="form-control"
                  id="menuPrice"
                  v-model="fprice"
                  required
                  pattern="[0-9,]+"
                  @input="checkIfNumber"
                />
                <div v-if="!isNumber" class="text-danger">
                  숫자만 입력해주세요.
                </div>
              </div>
              <!-- 파일 업로드 부분 주석 처리 -->
              <!--
              <div class="mb-3">
                <label class="form-label">파일 업로드</label>
                <input
                  type="file"
                  class="form-control"
                  id="fileUpload"
                  @change="handleFileUpload"
                />
              </div>
              -->
              <div class="mb-3">
                <label class="form-check-label">옵션 추가 여부:</label>
                <div class="form-check">
                  <input
                    type="radio"
                    id="optionYes"
                    class="form-check-input"
                    v-model="foption"
                    value="1"
                  />
                  <label for="optionYes" class="form-check-label"
                    >옵션 추가</label
                  >
                </div>
                <div class="form-check">
                  <input
                    type="radio"
                    id="optionNo"
                    class="form-check-input"
                    v-model="foption"
                    value="2"
                  />
                  <label for="optionNo" class="form-check-label"
                    >옵션 추가 안 함</label
                  >
                </div>
              </div>
            </form>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-bs-dismiss="modal"
            >
              Close
            </button>
            <button
              type="button"
              class="btn btn-primary"
              @click="saveChanges"
              :disabled="!isNumber"
            >
              Save changes
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "B_MenuPlusMD",
  props: ["bnum", "fnum"],

  data() {
    return {
      fname: "",
      finfo: "",
      fprice: "",
      foption: "2", // 기본값은 "옵션 추가 안 함"으로 설정
      isNumber: true,
    };
  },

  methods: {
    checkIfNumber() {
      const formattedPrice = this.fprice.replace(/\D/g, "");
      this.fprice = Number(formattedPrice).toLocaleString();
      this.isNumber = /^[0-9,]+$/.test(formattedPrice);
    },

    saveChanges() {
      if (!this.isNumber) return;

      const formData = new FormData();
      formData.append("fname", this.fname);
      formData.append("finfo", this.finfo);
      formData.append("fprice", this.fprice.replace(/,/g, ""));
      formData.append("foption", this.foption); // foption 값 추가
      formData.append("bnum", this.bnum); // bnum 값 추가

      axios
        .post("/MenuInsert", formData, {
          headers: {
            "Content-Type": "application/json",
          },
        })
        .then((response) => {
          console.log("메뉴 추가 성공:", response.data);
          // 추가된 메뉴 정보를 처리하는 코드 추가
        })
        .catch((error) => {
          console.error("메뉴 추가 실패:", error);
          // 오류 처리 코드 추가
        });
    },
  },
};
</script>
