<template>
  <div class="container mt-5" style="height: 800px; overflow-y: auto">
    <div class="row justify-content-center">
      <div class="col-md-7 col-lg-8">
        <h4 class="mb-3">가게정보 수정</h4>
        <h2>{{ shopName }}</h2>

        <form class="needs-validation" @submit.prevent="submitForm">
          <div class="row g-3">
            <div class="col-sm-6">
              <label for="ShopName" class="form-label">상호명:</label>
              <input
                type="text"
                class="form-control"
                id="ShopName"
                :value="shopName"
                readonly
                disabled
              />
              <div class="invalid-feedback">Valid first name is required.</div>
            </div>
            <div class="col-sm-6">
              <label for="category" class="form-label">분류:</label>
              <input
                type="text"
                class="form-control"
                id="category"
                :value="category"
                readonly
                disabled
              />
              <div class="invalid-feedback">Valid last name is required.</div>
            </div>

            <div class="col-12">
              <label for="time" class="form-label">운영시간</label>

              <input
                type="text"
                class="form-control"
                id="start"
                placeholder="운영시작시간"
                v-model="start"
                required
              />
              <input
                type="text"
                class="form-control"
                id="end"
                placeholder="운영종료시간"
                v-model="end"
                required
              />
              <div class="invalid-feedback">시간을 입력해주세요.</div>
            </div>

            <div class="form-group col-md-5">
              <label for="my_number">전화번호</label>
              <input
                type="tel"
                class="form-control"
                name="my_number"
                id="my_pnumber"
                maxlength="13"
                placeholder="전화번호"
                :value="phoneNumber"
                readonly
                disabled
              />
            </div>

            <div class="col-12">
              <label for="address" class="form-label">주소</label>
              <input
                type="text"
                class="form-control"
                id="address"
                :value="address"
                readonly
                disabled
              />
              <div class="invalid-feedback">
                Please enter your shipping address.
              </div>
            </div>
            <B_MenuPlusMD :bnum="bnum" />
            <B_MenuPluse :bnum="bnum" @menuSelected="handleMenuSelected" />

            <button type="submit" class="btn btn-primary">저장</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import B_MenuPluse from "@/components/business/MS_B_MenuPluse.vue";
import B_MenuPlusMD from "@/components/business/Ms_B_MenuPlusMD.vue";

import axios from "axios";

export default {
  name: "B_ShopRevise",

  components: {
    B_MenuPlusMD,
    B_MenuPluse,
  },
  data() {
    return {
      shop: [],
      category: "",
      start: "",
      end: "",
      bnum: 1003,
      shopName: "",
      phoneNumber: "",
      address: "",
    };
  },

  created() {
    this.fetchStoreInfo(this.bnum);
  },
  methods: {
    handleMenuSaved() {
      console.log("메뉴가 저장되었습니다.");
      // 저장된 후 수행할 작업을 여기에 추가합니다.
    },
    fetchStoreInfo(bnum) {
      axios
        .get(`/B_ShopDetail?bnum=${bnum}`)
        .then((response) => {
          this.shop = response.data;
          this.shopName = this.shop.sname;
          this.category = this.shop.fcategory_mikey;
          this.phoneNumber = this.shop.stel;
          this.address = this.shop.saddr;
          this.start = this.shop.start_time; // 문자열 그대로 저장
          this.end = this.shop.end_time; // 문자열 그대로 저장
          // this.fnum = this.shop.fnum;
        })
        .catch((error) => {
          console.error("가게 정보 가져오기 실패:", error);
        });
    },

    submitForm() {
      const formData = {
        bnum: this.bnum,
        start_time: this.start,
        end_time: this.end,
      };
      axios
        .post("/B_ShopTimeUpdate", formData)
        .then((response) => {
          console.log("서버 응답:", response.data);
        })
        .catch((error) => {
          console.error("서버 요청 실패:", error);
        });
    },
  },
};
</script>
